import React from "react";
import "../index.css";
import SearchIcon from "@mui/icons-material/Search";
import CallIcon from "@mui/icons-material/Call";

function Navbar() {
  return (
    <>
      <div className="flex justify-between ">
        <div className="m-4 flex justify-start content-center ml-8 lg:block">
          <a href="#" className="text-2xl bg-white ml-8 ">
            Micro<span className="text-red-500 text-3xl">Degree</span>
          </a>
          <a
            href="#"
            className=" ml-8 mt-1.5 items-center text-blue-700 text-2xl lg:block "
          >
            Courses
          </a>
          <form>
            <input
              className="ml-8 mt-2 border-solid border-2 rounded p-1 "
              type="text"
              placeholder="what do you wan to learn"
            />
          </form>
        </div>
        <div>
          <nav className="mr-8 mt-8 ">
            <a className="mr-8 text-2xl " href="#">
              More
            </a>
            <a
              className="mr-8 text-2xl text-red-500 border-solid border-red-500 border-2 rounded-md px-2 "
              href="#"
            >
              <CallIcon className="mr-2" />
              Contact Us
            </a>
            <a
              className="mr-8 text-2xl bg-red-600 border-solid rounded-md text-white px-4 py-1"
              href="#"
            >
              Login
            </a>
          </nav>
        </div>
      </div>
    </>
  );
}

export default Navbar;
